
import { csvParse } from  "d3-dsv";
import { timeParse } from "d3-time-format";


var yahoo = require("yahoo-finance-history");
global.fetch = require('node-fetch');
 
function parseData(parse) {
	return function(d) {
		d.date = parse(d.Date);
		d.open = +d.Open;
		d.high = +d.High;
		d.low = +d.Low;
		d.close = +d.Close;
		d.volume = +d.Volume;

		return d;
	};
}

const parseDate = timeParse("%Y-%m-%d");

export function getData() {
	
	const promiseMSFT = yahoo.getPriceHistory("msft")
		.then(data => csvParse(data.priceHistory, parseData(parseDate)))
		console.log(promiseMSFT);
	return promiseMSFT;
}
